<script>
import i18n from 'i18next';
import axios from '@/api/axios';
import { getHost } from '@/utils/exec';
import isQt from '@/utils/is-qt';
import { shoeViews, handbagViews } from './viewArr';

const apiStartRenderTask = (data, groupAppId) => {
  return axios({
    method: 'post',
    url: '/show/renderTask/start',
    headers: { groupAppId },
    data,
  });
};

export default {
  name: 'RenderSetting',
  dialogify: {
    props: {
      flex: 'column',
      width: isQt() ? '100%' : '680px',
      height: isQt() ? '100%' : 'auto',
      // height: '720px',
      // top: '5vh',
      top: 'auto',
      bottom: 'auto',
      title: i18n.t('model.begin_render'),
      modal: false,
      size: 'medium',
    },
    closeWith: 'model',
  },
  beforeDestroy() {
    this.toggleSelectedViewsAll(false);
  },
  // filters: {
  //   separate(str) {
  //     return str.split(':').join(' : ');
  //   },
  // },
  props: {
    // 相机模式 0 透视，1 正交
    cameraType: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      // radioSelected: '1:1',
      resolutionSelected: 'Preview',
      formatSelected: 'jpg',
      formats: [
        { type: 'jpg', label: 'jpg', tip: i18n.t('model.offline.format_jpg_tip') },
        { type: 'png', label: 'png', tip: i18n.t('model.offline.format_png_tip') },
      ],
      // radios: ['16:9', '4:3', '1:1'],
      // radioImgs: [
      //   require('@/assets/img/offline/radio-16-9.png'),
      //   require('@/assets/img/offline/radio-4-3.png'),
      //   require('@/assets/img/offline/radio-1-1.png'),
      // ],
      // radioResolutionMathers: {
      //   '16:9': ['1024*576', '1920*1080', '4000*2250'],
      //   '4:3': ['1024*768', '1920*1400', '4000*3000'],
      //   '1:1': ['1024*1024', '1920*1920', '4000*4000'],
      // },
      footModel: 'left',
      views: shoeViews,
      bagViews: handbagViews,
      panoramaViews: [
        {
          value: '1',
          name: i18n.t('model.turntable_mode'),
          icon: 'iconzhuanpanmoshi',
          disabled: false,
          tip: i18n.t('model.turntable_mode_tip'),
        },
        {
          value: '2',
          name: i18n.t('model.spherical_mode'),
          icon: 'iconqiuxingmoshi',
          tip: i18n.t('model.spherical_mode_tip'),
          disabled: true,
        },
      ],
      isSelectedPanoramaViews: null,
      submiting: false,
      currentTab: 'shoes',
    };
  },
  computed: {
    /** @return { [] } */
    resolutions() {
      // const resolution = this.radioResolutionMathers[this.radioSelected];
      const resolution = ['1024*1024', '1920*1920', '4000*4000'];
      return [
        { type: 'Preview', label: i18n.t('model.photoSize.preview'), radio: resolution[0] },
        { type: 'Standard', label: i18n.t('model.photoSize.standard'), radio: resolution[1] },
        { type: 'HD', label: i18n.t('model.photoSize.hd'), radio: resolution[2] },
      ];
    },
    // 不同比例下的图片地址
    // radioImgUrl() {
    //   return this.radioImgs[this.radios.indexOf(this.radioSelected)];
    // },
    // 是否全选了所有相机视角
    /** @return { boolean } */
    viewSelectedAll() {
      if (this.relateType === 3) {
        return this.bagViews.findIndex(view => !view.selected) < 0;
      }
      return this.modelViews.findIndex(view => !view.selected) < 0;
    },
    // 相机视角一个都没选（用于禁用提交）
    /** @return { boolean } */
    viewSelectedNone() {
      return this.views.findIndex(view => view.selected) < 0 && this.bagViews.findIndex(view => view.selected) < 0;
    },
    // 0表示模型是鞋子，3为包包，4是服饰
    /** @return { number } */
    relateType() {
      if (this.$route.query.relateType === '3') return 3;
      if (this.$route.query.relateType === '4') return 4;
      return 0;
    },
    /** @return { [] } */
    modelViews() {
      if (this.relateType === 3) return this.bagViews;
      if (this.relateType === 0 && this.footModel === 'right') {
        return this.views.slice(0, 8);
      }
      return this.views.slice(8);
    },
  },
  methods: {
    // 选择不同视角
    selectView(view, ignore) {
      if (ignore) return;
      this.isSelectedPanoramaViews = null;
      // const selectedViewsLength = this.views.filter(v => v.selected).length;
      // if (selectedViewsLength === 1 && view.selected) return;
      view.selected = !view.selected;
    },
    // 切换视角全选
    toggleSelectedViewsAll(isChecked) {
      this.isSelectedPanoramaViews = null;
      if (this.relateType === 0) {
        this.modelViews.forEach(view => {
          view.selected = isChecked;
        });
      } else if (this.relateType === 3) {
        this.bagViews.forEach(view => {
          view.selected = isChecked;
        });
      }
    },
    footModelChange(footModel) {
      this.views.forEach(view => {
        view.selected = false;
      });
    },
    selectPanoramaViews(index) {
      if (this.panoramaViews[index].disabled) return;
      this.views.forEach(v => { v.selected = false; });
      this.bagViews.forEach(v => { v.selected = false; });
      this.isSelectedPanoramaViews = index;
    },
    async submit() {
      const form = {
        shoeId: this.$route.query.modelId || this.$route.params.modelId,
        image: {
          width: this._astractResolutionWidth('width'),
          height: this._astractResolutionWidth('height'),
        },
        pictureRatioType: this._formatRadio(),
        cameras: this._mapCameras(),
        mode: this.isSelectedPanoramaViews === null ? 0 : 1,
        extension: this.formatSelected,
        relateType: this.relateType,
        cameraType: this.$route.params.cameraType || this.cameraType,
      };

      try {
        this.submiting = true;
        const host = await getHost();
        if (host) {
          const { workspaceId } = this.$route.query;
          // 客户端传的参数名是from而不是workspaceId
          // workspaceId = workspaceId || this.$route.query.from;
          await apiStartRenderTask(form, workspaceId);
          host.closeWin({
            success: true,
            data: 'render-save',
          });
        } else {
          this.$emit('confirm', form);
        }
      } catch (err) {
        this.$errHandler(err);
      } finally {
        this.submiting = false;
      }
    },
    _formatRadio() {
      // if (this.radioSelected === '16:9') return 1;
      // if (this.radioSelected === '4:3') return 2;
      return 3;
    },
    _astractResolutionWidth(type) {
      let result;
      this.resolutions.forEach(resolution => {
        if (resolution.type === this.resolutionSelected) {
          const [width, height] = resolution.radio.split('*');
          result = type === 'width' ? +width : +height;
        }
      });
      return result;
    },
    _mapCameras() {
      const result = [];
      if (this.relateType === 0) {
        this.views.forEach(view => {
          if (view.selected) result.push({ name: view.value });
        });
      } else if (this.relateType === 3) {
        this.bagViews.forEach(view => {
          if (view.selected) result.push({ name: view.value });
        });
      }
      return result;
    },
  },
};
</script>

